import { onMount } from "solid-js";
import weddingConfig from "../data/wedding-config.json";

interface MetaTagsProps {
  ogImageUrl?: string;
}

export default function MetaTags(props: MetaTagsProps) {
  const formatDate = (dateStr: string) => {
    const date = new Date(dateStr);
    const year = date.getFullYear();
    const month = date.getMonth() + 1;
    const day = date.getDate();
    return `${year} ${month} ${day}`;
  };

  onMount(() => {
    // Update dynamic meta tags
    const titleElement = document.querySelector("title");
    if (titleElement) {
      titleElement.textContent = `${weddingConfig.weddingInfo.groomFullName}  ${weddingConfig.weddingInfo.brideFullName}  `;
    }

    // Update OpenGraph meta tags
    const updateMetaTag = (property: string, content: string) => {
      let metaTag = document.querySelector(`meta[property="${property}"]`);
      if (!metaTag) {
        metaTag = document.createElement("meta");
        metaTag.setAttribute("property", property);
        document.head.appendChild(metaTag);
      }
      metaTag.setAttribute("content", content);
    };

    const weddingDate = formatDate(weddingConfig.weddingInfo.date);
    updateMetaTag(
      "og:title",
      `${weddingConfig.weddingInfo.groomFullName}  ${weddingConfig.weddingInfo.brideFullName} `
    );
    updateMetaTag(
      "og:description",
      `${weddingDate} ${weddingConfig.weddingInfo.location}   .`
    );

    if (props.ogImageUrl) {
      updateMetaTag("og:image", props.ogImageUrl);
    }
  });

  return null; // This component doesn't render anything
}
