export function useKakaoShare() {
  const shareToKakao = () => {
    if (!window.Kakao || !window.Kakao.isInitialized()) {
      alert('    .    .');
      return;
    }

    window.Kakao.Share.sendCustom({
      templateId: import.meta.env.VITE_KAKAO_TEMPLATE_ID
    });
  };
  
  const isKakaoReady = () => {
    return window.Kakao && window.Kakao.isInitialized();
  };

  return {
    shareToKakao,
    isKakaoReady
  };
} 